.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  min-height: 100vh;
  background-color: #f5f7fa;
  font-family: 'Roboto', 'Segoe UI', sans-serif;
  padding: 20px 0;
  overflow-x: hidden; /* Prevent horizontal scrolling */
}

.container {
  max-width: 800px;
  width: 95%;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  min-height: 600px; /* Reduced height */
  overflow: hidden; /* Prevent content from overflowing */
  margin: 0 auto; /* Center the container */
  box-sizing: border-box;
}

h1 {
  color: #ff4757;
  margin-bottom: 1rem;
}

h2 {
  color: #2f3542;
  margin: 1rem 0;
}

.instructions-section {
  margin-bottom: 10px;
}

.card-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; /* Center horizontally */
  margin: 10px 0 20px 0; /* Top and bottom margins */
  position: relative;
  min-height: 300px; /* Slightly reduced height */
  overflow: hidden; /* Prevent content from overflowing */
  padding: 0; /* Remove padding */
  width: 100%; /* Full width */
  box-sizing: border-box;
}

/* Card styling is now handled by styled-components in App.tsx */
.card p {
  font-size: 1.2rem;
  line-height: 1.6;
  margin: 0;
  text-align: left;
  width: 100%; /* Full width */
  box-sizing: border-box;
}

.controls-section {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 200px;
}

p {
  color: #333;
  font-size: 1.1rem;
  line-height: 1.5;
}

.instructions {
  color: #444;
  font-weight: 500;
  margin: 0.75rem 0;
}

small {
  font-size: 0.85rem;
  color: #666;
  display: block;
  margin-top: 5px;
}

.result-item {
  padding: 10px 0;
  border-bottom: 1px solid #eee;
}

.result-item:last-child {
  border-bottom: none;
}

.export-button {
  background-color: #3498db;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  margin-top: 20px;
  transition: background-color 0.3s ease;
}

.export-button:hover {
  background-color: #2980b9;
}

.keyboard-hint {
  margin-top: 15px;
  font-size: 0.85rem;
  color: #888;
  display: flex;
  justify-content: center;
  align-items: center;
}

.keyboard-hint kbd {
  background-color: #f8f9fa;
  border: 1px solid #d3d3d3;
  border-radius: 3px;
  box-shadow: 0 1px 1px rgba(0,0,0,.2);
  color: #333;
  display: inline-block;
  font-size: 0.8rem;
  font-weight: 700;
  line-height: 1;
  padding: 3px 6px;
  margin: 0 5px;
  white-space: nowrap;
}

kbd {
  background-color: #f7f7f7;
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 2px 5px;
  font-size: 0.8rem;
}

/* Add overflow control to the body and html */
body, html {
  overflow-x: hidden;
  width: 100%;
  margin: 0;
  padding: 0;
}

/* Results styling */
.result-item {
  margin: 10px 0;
  padding: 10px;
  border-radius: 5px;
  background-color: #f9f9f9;
  text-align: left;
}

.result-item.accepted {
  border-left: 3px solid #26de81;
}

.result-item.rejected {
  border-left: 3px solid #fd5068;
}

.classification {
  font-weight: bold;
  margin-right: 10px;
}

.classification.accepted {
  color: #26de81;
}

.classification.rejected {
  color: #fd5068;
}

.results {
  text-align: center;
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

.results h2 {
  color: #2f3542;
  margin-bottom: 30px;
}

.result-summary {
  background-color: #f9f9f9;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
}

.decimal-result {
  font-size: 2.5rem;
  font-weight: bold;
  color: #ff4757;
  margin: 20px 0;
  padding: 10px;
  background-color: #fff;
  border-radius: 8px;
  border: 2px solid #eee;
  display: inline-block;
  min-width: 150px;
}

.thank-you-message {
  margin-top: 20px;
  color: #555;
  font-size: 1.1rem;
}

/* Mobile responsiveness */
@media (max-width: 480px) {
  .App {
    padding: 10px 0;
  }
  
  .container {
    padding: 0.5rem;
  }
  
  .card-section {
    min-height: 280px;
    padding: 10px;
  }
  
  .instructions {
    margin: 0.5rem 0;
  }
}

/* Mobile responsiveness for results */
@media (max-width: 480px) {
  .results {
    padding: 10px;
  }
  
  .decimal-result {
    font-size: 2rem;
    min-width: 120px;
  }
  
  .thank-you-message {
    font-size: 1rem;
  }
}
